import { useCallback, useEffect, useState } from "react";
import { toast } from "react-hot-toast";
import { API_ENDPOINTS } from "../config/api";
import useApi from "./useApi";
import useWebSocket from "./useWebSocket";

/**
 * Hook personnalis pour grer les congs
 */
const useVacations = () => {
  const [vacations, setVacations] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const api = useApi();

  // Intgration WebSocket pour les mises  jour en temps rel
  const { socket, isConnected, sendMessage, notifyDataChange, fallbackMode } =
    useWebSocket();

  // couter les mises  jour WebSocket
  useEffect(() => {
    if (socket && isConnected) {
      // Fonction pour traiter les messages WebSocket
      const handleWebSocketMessage = (event) => {
        try {
          const data = JSON.parse(event.data);

          // Si le message concerne une mise  jour de cong
          if (data.type === "VACATION_UPDATED" && data.vacation) {
            setVacations((prevVacations) => {
              // Vrifier si le cong existe dj
              const exists = prevVacations.some(
                (vacation) => vacation.id === data.vacation.id
              );

              if (exists) {
                // Mettre  jour le cong existant
                return prevVacations.map((vacation) =>
                  vacation.id === data.vacation.id ? data.vacation : vacation
                );
              } else {
                // Ajouter le nouveau cong
                return [...prevVacations, data.vacation];
              }
            });

            toast.info("Demande de cong mise  jour en temps rel");
          }

          // Si le message concerne une suppression de cong
          if (data.type === "VACATION_DELETED" && data.vacationId) {
            setVacations((prevVacations) =>
              prevVacations.filter(
                (vacation) => vacation.id !== data.vacationId
              )
            );

            toast.info("Demande de cong supprime en temps rel");
          }
        } catch (error) {
          console.error(
            "Erreur lors du traitement du message WebSocket:",
            error
          );
        }
      };

      // Ajouter l'couteur d'vnements
      socket.addEventListener("message", handleWebSocketMessage);

      // Nettoyer l'couteur lors du dmontage
      return () => {
        socket.removeEventListener("message", handleWebSocketMessage);
      };
    }
  }, [socket, isConnected]);

  /**
   * Fonction utilitaire pour ressayer une requte API
   * @param {Function} apiCall - Fonction d'appel API  ressayer
   * @param {number} maxRetries - Nombre maximum de tentatives
   * @param {number} delay - Dlai entre les tentatives (en ms)
   * @returns {Promise} - Rsultat de l'appel API
   */
  const retryApiCall = useCallback(
    async (apiCall, maxRetries = 3, delay = 1000) => {
      let lastError = null;
      let retryCount = 0;

      while (retryCount < maxRetries) {
        try {
          return await apiCall();
        } catch (err) {
          lastError = err;
          retryCount++;

          // Si c'est la dernire tentative, ne pas attendre
          if (retryCount < maxRetries) {
            // Utiliser un dlai exponentiel
            const retryDelay = delay * Math.pow(2, retryCount - 1);
            console.log(
              `Tentative ${retryCount}/${maxRetries} choue, nouvelle tentative dans ${retryDelay}ms`
            );
            await new Promise((resolve) => setTimeout(resolve, retryDelay));
          }
        }
      }

      // Si toutes les tentatives ont chou, lancer l'erreur
      throw lastError;
    },
    []
  );

  // Fonction pour charger les congs
  const fetchVacations = useCallback(async () => {
    let retryCount = 0;
    const maxRetries = 3;

    const loadVacations = async () => {
      if (retryCount >= maxRetries) {
        setError(
          "Erreur lors du chargement des congs aprs plusieurs tentatives"
        );
        setLoading(false);
        return;
      }

      try {
        console.log("Chargement des congs...");
        const token = localStorage.getItem("token");

        if (!token) {
          console.error("Token d'authentification manquant");
          setError("Vous devez tre connect pour accder  ces donnes");
          setLoading(false);
          return;
        }

        const data = await api.get(API_ENDPOINTS.VACATIONS);
        console.log("Donnes des congs reues:", data);

        if (Array.isArray(data)) {
          setVacations(data);
          setError(null);
        } else {
          console.error("Format de donnes invalide:", data);
          setError("Format de donnes invalide");
        }
        setLoading(false);
      } catch (err) {
        console.error("Erreur lors du chargement des congs:", err);
        setError(err.message || "Erreur lors du chargement des congs");

        // Ressayer avec un dlai exponentiel
        retryCount++;
        setTimeout(loadVacations, 1000 * Math.pow(2, retryCount));
      }
    };

    setLoading(true);
    setError(null);
    await loadVacations();
  }, [api]);

  // Charger les congs au montage du composant
  useEffect(() => {
    fetchVacations();
  }, [fetchVacations]);

  /**
   * Vrifie si la base de donnes est accessible
   */
  const checkDatabaseConnection = useCallback(async () => {
    try {
      // Faire une requte simple pour vrifier la connexion
      const response = await api.get(API_ENDPOINTS.VACATIONS);

      // Si la requte choue avec une erreur de connexion, la base de donnes n'est pas accessible
      if (!response.ok && (response.status === 0 || response.status >= 500)) {
        console.error("La base de donnes semble inaccessible:", response);
        return {
          connected: false,
          error: "La connexion  la base de donnes a chou",
        };
      }

      return { connected: true };
    } catch (error) {
      console.error(
        "Erreur lors de la vrification de la connexion  la base de donnes:",
        error
      );
      return {
        connected: false,
        error:
          "Erreur lors de la vrification de la connexion  la base de donnes",
      };
    }
  }, [api]);

  /**
   * Cre une nouvelle demande de cong
   */
  const createVacation = useCallback(
    async (vacationData) => {
      setLoading(true);
      setError(null);

      try {
        // Vrifier que les donnes essentielles sont prsentes
        if (!vacationData.employeeId) {
          toast.error("L'identifiant de l'employ est requis");
          setLoading(false);
          return {
            success: false,
            error: "L'identifiant de l'employ est requis",
          };
        }

        if (!vacationData.startDate || !vacationData.endDate) {
          toast.error("Les dates de dbut et de fin sont requises");
          setLoading(false);
          return {
            success: false,
            error: "Les dates de dbut et de fin sont requises",
          };
        }

        // Convertir les dates en objets Date
        const startDate = new Date(vacationData.startDate);
        const endDate = new Date(vacationData.endDate);

        // Vrifier que les dates sont valides
        if (isNaN(startDate.getTime()) || isNaN(endDate.getTime())) {
          toast.error("Les dates fournies ne sont pas valides");
          setLoading(false);
          return {
            success: false,
            error: "Les dates fournies ne sont pas valides",
          };
        }

        // Vrifier que la date de dbut est avant la date de fin
        if (startDate > endDate) {
          toast.error("La date de dbut doit tre antrieure  la date de fin");
          setLoading(false);
          return {
            success: false,
            error: "La date de dbut doit tre antrieure  la date de fin",
          };
        }

        // Formater les donnes pour l'API - utiliser directement snake_case pour viter les conversions
        const formattedData = {
          employee_id: String(vacationData.employeeId),
          type: vacationData.type || "paid",
          start_date: startDate.toISOString().split("T")[0],
          end_date: endDate.toISOString().split("T")[0],
          reason: vacationData.reason || "",
          status: "pending",
          // Supprimer les champs qui n'existent pas dans la table
          // approved_by et approved_at ne sont pas dans la table
          // rejected_by et rejected_at ne sont pas dans la table
        };

        // Vrifier que les donnes sont valides pour le backend
        if (!formattedData.employee_id) {
          console.error("Erreur: employee_id manquant aprs formatage");
          toast.error("L'identifiant de l'employ est requis");
          return {
            success: false,
            error: "Erreur de formatage des donnes: employee_id manquant",
          };
        }

        // Vrifier que les dates sont au bon format
        if (!formattedData.start_date || !formattedData.end_date) {
          console.error("Erreur: dates mal formates", {
            start_date: formattedData.start_date,
            end_date: formattedData.end_date,
          });
          toast.error("Les dates de dbut et de fin sont requises");
          return {
            success: false,
            error: "Erreur de formatage des dates",
          };
        }

        // Vrifier que le type est valide (paid, unpaid, sick, other)
        const validTypes = ["paid", "unpaid", "sick", "other"];
        if (!validTypes.includes(formattedData.type)) {
          console.warn(
            `Type de cong non standard: ${formattedData.type}, utilisation de 'paid' par dfaut`
          );
          formattedData.type = "paid";
        }

        console.log("Donnes formates pour l'API:", formattedData);

        // Rcuprer le token d'authentification
        const token = localStorage.getItem("token");
        console.log("Token d'authentification prsent:", !!token);

        // Appel API avec retry en cas d'chec
        const apiCall = async () => {
          try {
            const response = await api.post(
              API_ENDPOINTS.VACATIONS,
              formattedData
            );
            console.log("Rponse de l'API:", response);

            // Mettre  jour l'tat local
            setVacations((prevVacations) => [...prevVacations, response]);

            // Notifier le succs
            toast.success("Demande de cong cre avec succs");

            // Envoyer une notification WebSocket
            if (socket && socket.readyState === WebSocket.OPEN) {
              socket.send(
                JSON.stringify({
                  type: "VACATION_CREATED",
                  data: response,
                })
              );
            }

            return { success: true, data: response };
          } catch (error) {
            console.error(
              "Erreur lors de la cration de la demande de cong:",
              error
            );

            // Afficher un message d'erreur plus prcis si disponible
            const errorMessage =
              error.response?.data?.message ||
              error.response?.data?.error ||
              error.message ||
              "Erreur lors de la cration de la demande de cong";

            toast.error(errorMessage);
            setError(errorMessage);

            throw error;
          }
        };

        // Excuter l'appel API avec retry
        const result = await retryApiCall(apiCall, 2, 1000);
        setLoading(false);
        return result;
      } catch (error) {
        console.error(
          "Erreur finale lors de la cration de la demande de cong:",
          error
        );
        setLoading(false);
        setError(
          error.message || "Erreur lors de la cration de la demande de cong"
        );
        return { success: false, error: error.message };
      }
    },
    [
      api,
      retryApiCall,
      socket,
      isConnected,
      notifyDataChange,
      fallbackMode,
      checkDatabaseConnection,
    ]
  );

  /**
   * Met  jour une demande de cong existante
   */
  const updateVacation = useCallback(
    async (id, vacationData) => {
      try {
        const apiCall = async () => {
          const response = await api.put(
            `${API_ENDPOINTS.VACATIONS}/${id}`,
            vacationData
          );

          if (!response.ok) {
            throw new Error(
              response.data?.message ||
                "Erreur lors de la mise  jour de la demande de cong"
            );
          }

          return response.data;
        };

        // Utiliser la fonction de retry
        const data = await retryApiCall(apiCall);

        setVacations((prev) =>
          prev.map((vacation) => (vacation.id === id ? data : vacation))
        );

        // Notifier les autres clients via WebSocket
        if (!fallbackMode && isConnected) {
          notifyDataChange("vacation", "update", id);
        }

        toast.success("Demande de cong mise  jour avec succs");
        return { success: true, vacation: data };
      } catch (err) {
        console.error(
          "Erreur lors de la mise  jour de la demande de cong:",
          err
        );
        const errorMessage = err.message || "Erreur inconnue";
        toast.error(
          `Erreur lors de la mise  jour de la demande de cong: ${errorMessage}`
        );
        return { success: false, error: errorMessage };
      }
    },
    [api, retryApiCall, fallbackMode, isConnected, notifyDataChange]
  );

  /**
   * Supprime une demande de cong
   */
  const deleteVacation = useCallback(
    async (id) => {
      try {
        const apiCall = async () => {
          const response = await api.delete(`${API_ENDPOINTS.VACATIONS}/${id}`);

          if (!response.ok) {
            throw new Error(
              response.data?.message ||
                "Erreur lors de la suppression de la demande de cong"
            );
          }

          return response.data;
        };

        // Utiliser la fonction de retry
        await retryApiCall(apiCall);

        setVacations((prev) => prev.filter((vacation) => vacation.id !== id));

        // Notifier les autres clients via WebSocket
        if (!fallbackMode && isConnected) {
          notifyDataChange("vacation", "delete", id);
        }

        toast.success("Demande de cong supprime avec succs");
        return { success: true };
      } catch (err) {
        console.error(
          "Erreur lors de la suppression de la demande de cong:",
          err
        );
        const errorMessage = err.message || "Erreur inconnue";
        toast.error(
          `Erreur lors de la suppression de la demande de cong: ${errorMessage}`
        );
        return { success: false, error: errorMessage };
      }
    },
    [api, retryApiCall, fallbackMode, isConnected, notifyDataChange]
  );

  /**
   * Approuve ou rejette une demande de cong
   */
  const updateVacationStatus = useCallback(
    async (id, status, comment = "") => {
      try {
        const apiCall = async () => {
          const response = await api.put(
            `${API_ENDPOINTS.VACATIONS}/${id}/status`,
            {
              status,
              comment,
            }
          );

          if (!response.ok) {
            throw new Error(
              response.data?.message ||
                `Erreur lors de la ${
                  status === "approved" ? "validation" : "rejet"
                } de la demande de cong`
            );
          }

          return response.data;
        };

        // Utiliser la fonction de retry
        const data = await retryApiCall(apiCall);

        setVacations((prev) =>
          prev.map((vacation) => (vacation.id === id ? data : vacation))
        );

        // Notifier les autres clients via WebSocket
        if (!fallbackMode && isConnected) {
          notifyDataChange("vacation", "update", id);
        }

        toast.success(
          `Demande de cong ${
            status === "approved" ? "approuve" : "rejete"
          } avec succs`
        );
        return { success: true, vacation: data };
      } catch (err) {
        console.error(
          `Erreur lors de la ${
            status === "approved" ? "validation" : "rejet"
          } de la demande de cong:`,
          err
        );
        const errorMessage = err.message || "Erreur inconnue";
        toast.error(
          `Erreur lors de la ${
            status === "approved" ? "validation" : "rejet"
          } de la demande de cong: ${errorMessage}`
        );
        return { success: false, error: errorMessage };
      }
    },
    [api, retryApiCall, fallbackMode, isConnected, notifyDataChange]
  );

  /**
   * Filtre les congs par statut
   * @param {string|null} status - Le statut  filtrer (pending, approved, rejected) ou null pour tous
   * @returns {Array} - Les congs filtrs
   */
  const getVacationsByStatus = useCallback(
    (status) => {
      if (!status) return vacations;
      return vacations.filter((vacation) => vacation.status === status);
    },
    [vacations]
  );

  return {
    vacations,
    loading,
    error,
    fetchVacations,
    createVacation,
    updateVacation,
    deleteVacation,
    updateVacationStatus,
    getVacationsByStatus,
    approveVacation: (id) => updateVacationStatus(id, "approved"),
    rejectVacation: (id, comment) =>
      updateVacationStatus(id, "rejected", comment),
  };
};

export default useVacations;
