import { useCallback, useEffect, useState } from "react";
import { toast } from "react-hot-toast";
import { API_ENDPOINTS } from "../config/api";
import useApi from "./useApi";

/**
 * Hook personnalis pour grer les dpartements
 * Rcupre les dpartements depuis la base de donnes via les employs
 */
const useDepartments = () => {
  const [departments, setDepartments] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const api = useApi();

  /**
   * Rcupre tous les dpartements depuis les employs existants
   */
  const fetchDepartments = useCallback(async () => {
    try {
      setLoading(true);
      const response = await api.get(API_ENDPOINTS.DEPARTMENTS.BASE);

      if (response.ok) {
        setDepartments(response.data);
        setError(null);
      } else {
        throw new Error(
          response.data?.message || "Erreur lors du chargement des dpartements"
        );
      }
    } catch (err) {
      console.error("Erreur lors du chargement des dpartements:", err);
      setError("Erreur lors du chargement des dpartements");
      toast.error("Erreur lors du chargement des dpartements");
    } finally {
      setLoading(false);
    }
  }, [api]);

  const createDepartment = useCallback(
    async (departmentData) => {
      try {
        const response = await api.post(
          API_ENDPOINTS.DEPARTMENTS.BASE,
          departmentData
        );

        if (response.ok) {
          setDepartments((prev) => [...prev, response.data]);
          toast.success("Dpartement cr avec succs");
          return { success: true, department: response.data };
        } else {
          throw new Error(
            response.data?.message ||
              "Erreur lors de la cration du dpartement"
          );
        }
      } catch (err) {
        console.error("Erreur lors de la cration du dpartement:", err);
        toast.error("Erreur lors de la cration du dpartement");
        return { success: false, error: err.message };
      }
    },
    [api]
  );

  const updateDepartment = useCallback(
    async (id, departmentData) => {
      try {
        const response = await api.put(
          API_ENDPOINTS.DEPARTMENTS.BY_ID(id),
          departmentData
        );

        if (response.ok) {
          setDepartments((prev) =>
            prev.map((dept) =>
              dept.id === id ? { ...dept, ...response.data } : dept
            )
          );
          toast.success("Dpartement mis  jour avec succs");
          return { success: true, department: response.data };
        } else {
          throw new Error(
            response.data?.message ||
              "Erreur lors de la mise  jour du dpartement"
          );
        }
      } catch (err) {
        console.error("Erreur lors de la mise  jour du dpartement:", err);
        toast.error("Erreur lors de la mise  jour du dpartement");
        return { success: false, error: err.message };
      }
    },
    [api]
  );

  const deleteDepartment = useCallback(
    async (id) => {
      try {
        const response = await api.delete(API_ENDPOINTS.DEPARTMENTS.BY_ID(id));

        if (response.ok) {
          setDepartments((prev) => prev.filter((dept) => dept.id !== id));
          toast.success("Dpartement supprim avec succs");
          return { success: true };
        } else {
          throw new Error(
            response.data?.message ||
              "Erreur lors de la suppression du dpartement"
          );
        }
      } catch (err) {
        console.error("Erreur lors de la suppression du dpartement:", err);
        toast.error("Erreur lors de la suppression du dpartement");
        return { success: false, error: err.message };
      }
    },
    [api]
  );

  /**
   * Rcupre un dpartement par son ID
   */
  const getDepartmentById = useCallback(
    (id) => {
      return departments.find((dept) => dept.id === id) || null;
    },
    [departments]
  );

  /**
   * Rcupre un dpartement par son nom
   */
  const getDepartmentByName = useCallback(
    (name) => {
      return departments.find((dept) => dept.name === name) || null;
    },
    [departments]
  );

  // Charger les dpartements au montage du composant
  useEffect(() => {
    fetchDepartments();
  }, [fetchDepartments]);

  return {
    departments,
    loading,
    error,
    fetchDepartments,
    createDepartment,
    updateDepartment,
    deleteDepartment,
    getDepartmentById,
    getDepartmentByName,
  };
};

export default useDepartments;
