import axios from "axios";
import { API_ENDPOINTS } from "../config/api";

/**
 * Utilitaire pour enregistrer les activits dans la base de donnes
 */
class ActivityLogger {
  /**
   * Enregistre une activit dans la base de donnes
   * @param {string} type - Type d'activit (create, update, delete, approve, reject, login, logout)
   * @param {string} entity_type - Type d'entit concerne (employee, schedule, vacation, user, etc.)
   * @param {string} entity_id - ID de l'entit concerne
   * @param {string} description - Description de l'activit
   * @param {object} userData - Donnes de l'utilisateur qui a effectu l'action
   * @param {object} details - Dtails supplmentaires au format JSON
   * @returns {Promise<object>} - Promesse avec les donnes de l'activit cre
   */
  static async logActivity(
    type,
    entity_type,
    entity_id,
    description,
    userData,
    details = {}
  ) {
    try {
      const activityData = {
        type,
        entity_type,
        entity_id,
        description,
        userId: userData?.id || null,
        userName:
          userData?.first_name && userData?.last_name
            ? `${userData.first_name} ${userData.last_name}`
            : userData?.username || "Utilisateur inconnu",
        details,
        timestamp: new Date().toISOString(),
      };

      console.log("Enregistrement d'une nouvelle activit:", activityData);

      const token = localStorage.getItem("token");
      const response = await axios.post(
        API_ENDPOINTS.ACTIVITIES.LOG,
        activityData,
        {
          headers: {
            "Content-Type": "application/json",
            ...(token && { Authorization: `Bearer ${token}` }),
          },
        }
      );

      if (response.status === 200 || response.status === 201) {
        console.log("Activit enregistre avec succs:", response.data);
        return response.data;
      } else {
        console.error(
          "Erreur lors de l'enregistrement de l'activit:",
          response.data?.message
        );
        return null;
      }
    } catch (error) {
      console.error("Erreur lors de l'enregistrement de l'activit:", error);
      return null;
    }
  }

  /**
   * Enregistre une activit de cration
   */
  static async logCreation(
    entity_type,
    entity_id,
    description,
    userData,
    details = {}
  ) {
    return this.logActivity(
      "create",
      entity_type,
      entity_id,
      description,
      userData,
      details
    );
  }

  /**
   * Enregistre une activit de mise  jour
   */
  static async logUpdate(
    entity_type,
    entity_id,
    description,
    userData,
    details = {}
  ) {
    return this.logActivity(
      "update",
      entity_type,
      entity_id,
      description,
      userData,
      details
    );
  }

  /**
   * Enregistre une activit de suppression
   */
  static async logDeletion(
    entity_type,
    entity_id,
    description,
    userData,
    details = {}
  ) {
    return this.logActivity(
      "delete",
      entity_type,
      entity_id,
      description,
      userData,
      details
    );
  }

  /**
   * Enregistre une activit d'approbation
   */
  static async logApproval(
    entity_type,
    entity_id,
    description,
    userData,
    details = {}
  ) {
    return this.logActivity(
      "approve",
      entity_type,
      entity_id,
      description,
      userData,
      details
    );
  }

  /**
   * Enregistre une activit de rejet
   */
  static async logRejection(
    entity_type,
    entity_id,
    description,
    userData,
    details = {}
  ) {
    return this.logActivity(
      "reject",
      entity_type,
      entity_id,
      description,
      userData,
      details
    );
  }

  /**
   * Enregistre une activit de connexion
   */
  static async logLogin(userId, description, userData, details = {}) {
    return this.logActivity(
      "login",
      "user",
      userId,
      description,
      userData,
      details
    );
  }

  /**
   * Enregistre une activit de dconnexion
   */
  static async logLogout(userId, description, userData, details = {}) {
    return this.logActivity(
      "logout",
      "user",
      userId,
      description,
      userData,
      details
    );
  }
}

export default ActivityLogger;
